@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;700&display=swap");
.default {
  margin: 1em 0;
  min-width: 300px;
}
.default tr {
  border-top: 1px solid #ddd;
  border-bottom: 1px solid #ddd;
}
.default th {
  display: none;
}
.default td {
  display: block;
}
.default td:first-child {
  padding-top: 0.5em;
}
.default td:last-child {
  padding-bottom: 0.5em;
}
.default td:before {
  content: attr(data-th) ": ";
  font-weight: bold;
  width: 6.5em;
  display: inline-block;
}
@media (min-width: 480px) {
  .default td:before {
    display: none;
  }
}
.default th,
.default td {
  text-align: left;
}
@media (min-width: 480px) {
  .default th,
  .default td {
    display: table-cell;
    padding: 0.25em 0.5em;
  }
  .default th:first-child,
  .default td:first-child {
    padding-left: 0;
  }
  .default th:last-child,
  .default td:last-child {
    padding-right: 0;
  }
}
* {
  margin: 0;
  padding: 0;
  border: 0;
}
body {
  font-family: Montserrat, sans-serif;
  -webkit-font-smoothing: antialiased;
  text-rendering: optimizeLegibility;
  color: #444;
  background: #eee;
  border: 0;
  height: 100%;
}
h1 {
  font-weight: normal;
  letter-spacing: -1px;
  color: #34495e;
}
a {
  text-decoration: none;
  transition: all 0.6s ease;
}
a:hover {
  transition: all 0.6s ease;
}
.sidebar {
  position: fixed;
  width: 6em;
  height: 100%;
  top: 0;
  overflow: hidden;
  background-color: #19222a;
  transform: translateZ(0);
  visibility: visible;
  transition-duration: 0.3s;
}
.sidebar:hover {
  width: 17em;
}
.sidebar:hover .logo-menu .text-navbar {
  opacity: 1;
  visibility: visible;
}
.container-logo {
  padding: 0.75em 0.3em;
}
.container-logo img {
  width: auto;
  height: auto;
}

.logo-menu {
  display: flex;
  padding: 0.75em 1.4em;
}
.logo-menu img {
  width: 40px;
  height: 40px;
}
.sidebar-nav {
  position: fixed;
  background-color: #19222a;
  height: 100%;
  font-weight: 400;
  font-size: 1.2em;
  overflow: auto;
  padding-bottom: 6em;
  z-index: 9;
  overflow: hidden;
  -webkit-overflow-scrolling: touch;
}
.sidebar-nav ul {
  list-style: none;
  display: block;
  padding: 0;
  margin: 0;
}
.sidebar-nav ul li {
  margin-left: 0;
  padding-left: 0;
  display: inline-block;
  width: 100%;
}
.sidebar-nav ul li .logo-menu .text-navbar {
  font-size: 0.75em;
  padding: 1.05em 1em;
  opacity: 0;
  visibility: hidden;
}
.sidebar-nav ul li a {
  color: rgba(255, 255, 255, 0.9);
  position: relative;
  display: block;
}
.sidebar-nav ul li i {
  font-size: 1.8em;
  padding-right: 0.5em;
  width: 9em;
  display: inline;
  vertical-align: middle;
}
.sidebar-nav > ul > li > a:after {
  content: "\f125";
  font-family: ionicons;
  width: 10px;
  color: #fff;
  position: absolute;
  right: 0.75em;
  top: 45%;
}
.sidebar-nav .nav-flyout {
  position: absolute;
  background-color: #080d11;
  z-index: 9;
  left: 5em;
  top: 0;
  transform: translateX(100%);
  transition: all 0.5s ease;
}
.sidebar-nav .nav-flyout a {
  font-size: 0.6em;
  padding: 0.7em 1em;
}
.sidebar-nav .nav-flyout a:hover {
  background-color: rgba(255, 255, 255, 0.05);
}
.sidebar-nav ul > li:hover .nav-flyout {
  transform: translateX(0);
  transition: all 0.5s ease;
}
.sidebar-nav .logo-menu:hover {
  background-color: rgba(0, 0, 0, 0.9);
}
.nav-flyout {
  overflow: scroll;
  overflow-x: hidden;
  height: 70%;
}
.nav-flyout::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}
.nav-flyout::-webkit-scrollbar-thumb {
  background: #da790a;
  border-radius: 4px;
}
.nav-flyout::-webkit-scrollbar-thumb:hover {
  background: #eb8a1c;
  box-shadow: 0 0 2px 1px rgba(0, 0, 0, 0.2);
}
.nav-flyout::-webkit-scrollbar-thumb:active {
  background-color: #ff8800;
}
.contenedor-tabla {
  margin-left: 6em;
  margin-top: 1.5em;
}
.container th h1 {
  font-weight: bold;
  font-size: 1em;
  color: #00afe4;
}
.container td {
  font-weight: normal;
  font-size: 1em;
  box-shadow: 0 2px 2px -2px #0e1119;
}
.container {
  text-align: left;
  overflow: hidden;
  width: 80%;
  margin: 0 auto;
  display: table;
  padding: 0 0 8em 0;
}
.container th,
.container td {
  padding-left: 5px;
  padding-top: 5px;
  padding-right: 5px;
  padding-bottom: 5px;
}
.container tr:nth-child(odd) {
  background-color: #ffffff;
}
.container tr:nth-child(even) {
  background-color: #ffffff;
}

.titulotabla {
  display: flex;
  justify-content: center;
  margin-top: 30px;
  margin-left: 6em;
}
.footer {
  display: flex;
  justify-content: center;
  bottom: 0;
  padding-top: 5px;
  padding-bottom: 17px;
  background-color: #e2e3e4;
  margin-left: 6em;
}
.social_footer_ul {
  display: flex;
  flex-direction: row;
  list-style: none;
  align-items: center;
}
.footerli {
  display: flex;
  align-items: center;
  padding-left: 25px;
}
.footerimg {
  width: 30px;
  height: 30px;
  margin-left: 5px;
}
.footera {
  display: flex;
  align-items: center;
}
.sectiontables {
  min-height: 91.5vh;
}
.Home {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 80vh;
  margin-top: 30px;
  margin-left: 6em;
}
.hometitle {
  font-size: 35px;
  text-align: center;
}
.homep {
  font-size: 20px;
  width: 80%;
  margin-top: 20px;
  text-align: justify;
}

@media (max-width: 720px) {
  .priority-1,
  .priority-6 {
    display: none;
  }

  .hometitle {
    font-size: 25px;
  }

  .homep {
    margin-top: 18px;
  }
}
@media (max-width: 580px) {
  .priority-1,
  .priority-6,
  .priority-5 {
    display: none;
  }
  .container th,
  .container td {
    padding-left: 3px;
    padding-top: 3px;
    padding-right: 3px;
    padding-bottom: 3px;
    font-size: 0.8em;
  }

  .titulotabla {
    font-size: 16px;
  }

  .hometitle {
    font-size: 20px;
  }

  .homep {
    margin-top: 16px;
  }

  .footertext {
    display: none;
  }
}
